
.container{
    display: grid;
    place-items: center;
    width: 100vw;
    min-height: calc(100vh - 70px);

    
  /* background-image: url('../../images/wave-4.svg'); */
  /* background-position: left; */
  background-repeat: no-repeat;
  /* background-size: contain; */
  background-size: cover;
}
.container .file{
    display: none;
}
.add_container{
     margin-top: 30px;
    
}
.add_container > h3{
    font-size: 22px;
    color: rgb(112, 112, 112);
    font-family: Arial, Helvetica, sans-serif;
    
}

.data_container{
    margin-top: 10px;
    border: 3px solid lightgray;
    padding: 15px;
    width: 500px;
    height: 200px;
    display: grid;
    place-items: center;
    border-radius: 10px;
    cursor: pointer;

}
@media (max-width:767px) {
    .data_container {
        width: 400px;
    }
    
}
@media (max-width:467px) {
    .data_container {
        width: 300px;
    }
    
}
.data_container > i {
    font-size: 32px;
    color: #577BC1;
    
}
.data_container > h4{
    font-size: 22px;
    color: rgb(112, 112, 112);
    font-family: Arial, Helvetica, sans-serif;


}
.data_container .imgs_number {
    color: white;
    background-color: #577BC1;
    padding: 10px 20px;
    border-radius: 8px;
}
.details form input {
    border: 3px solid lightgray;
    border-radius: 6px;
    
}
.details form input::placeholder{
    font-size: 20px;
    margin-top: 10px;
    color: gray;

}
.details form > div{
    margin: 20px 0;
    position: relative;
}

.details form > div > input {
    width: 100%;
    height: 50px;
    padding: 10px;
    font-size: 20px;
    background-color: transparent;
    outline: none;
    color: white;
}
.details form > div > input:focus{
    border-color: #577BC1;
    /* border-color: black; */
}
.details .category i{
   position: absolute;
   right: 20px;
   top: 50%;
   transform: translateY(-50%);
   color: gray;
   font-size: 22px;
}

.details form {
    position: relative;
}


.details .category span{
    font-size: 20px;

}

.details .place i {
    position: absolute;
    top: 50%;
    right: 20px;
    transform: translateY(-50%);
    color: gray;
    font-size: 20px;
}
.details .save_article {
    display: grid;
    place-items: center;
}

@media (min-width:568px) {
    .details .save_article button {
        border: none;
        outline: none;
        padding: 10px 15px;
        font-size: 20px;
        /* color: gray; */
        cursor: pointer;
        border-radius: 7px;
        transition: .3s;
        border: 2px solid #577BC1;
    
    }
    .details .save_article button a {
        text-decoration: none;
        color: #577BC1;
        width: 100%;
        height: 100%;
    }
    .details .save_article button:hover{
        background-color: #577BC1;
        color: white;
    
    }
    .details .save_article button:hover a{
        
        color: white;
    
    }
    
}
@media (max-width:567px) {
    .details .save_article button {
        border: 2px solid white;
        background-color: transparent;
        color: white;
    }
    .details .save_article button a {
        color: white;
        
        width: 100%;
        height: 100%;
    }
}