*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
  
}
.App{
  max-width: 100vw;
  /* height: 100vh; */
  overflow-x: hidden;
  transition: .4s;

  background-color: rgb(34, 34, 34);
  


}

.App.of{
  max-height: 100vh;
  overflow: hidden;
  /* background-color: rgb(219, 216, 216); */
  /* background-color: rgb(51, 51, 51); */


}


.header{
    width: 100vw;
    transition: .3s;
    max-width: 100vw;
    height: 70px;
    
        
}

.header.of > .header__container{ 
    background-color: #2e3e5e;
    
}
.header.of > .header__container input,
.header.of > .header__container .search {
    /* background-color: rgb(221, 220, 220); */
    /* color: lightgray; */
    opacity: .6;
    
}

.header__container{
    background-color: #577BC1;
    display: flex;
    align-items: center;
    justify-content: space-around;
    min-height: 70px;
    max-height: 70px;
    /* padding: 0 10px; */
    position: relative;
    position: fixed;
    width: 100%;
    z-index: 100;

}
.header__container a {
    text-decoration: none !important;
    
}
@media (max-width:567px) {
    .header__container {
        justify-content: space-between;
        padding: 0 10px;
    }
    
}
.header__container .logo{
    display: flex;
    align-items: center !important;
    cursor: pointer;
}
.header__container .logo >i {
    font-size: 30px;
    color: white;
    margin-right: 5px;
}
.header__container .logo >h3 {
    color: white;
    font-size: 20px;
}
@media (max-width:667px) {
    .header__container .logo >h3 {
        display: none;
    }
    
}


.header__container .search form{
    display: flex;
    align-items: center;
    
}
.header__container .search input{

    height: 35px;
    padding: 10px;
    font-size: 17px;
    border: 2px solid white;
    outline: none;
    border-radius: 5px 0 0 5px;


}
@media (min-width:992px) {
    .header__container .search input{
        min-width: 300px;
    }
    
}

@media (min-width:768px) {
    .header__container .search input{
        min-width: 400px;
    }
    
}
@media (max-width:767px) {
    .header__container .search input{
        min-width: 100px;
         
    }
    
}
@media (max-width:567px) {
    .header__container .search input{
        width: 200px;
        /* margin-left: 5px; */
        
    
        
    }
    
}
@media (max-width:321px) {
    .header__container .search input{
        width: 100px;
        /* max-width: 70px; */
        /* background-color: red; */
        
    }
    
}
.header__container .search  button{
    border: none;
    outline: none;
    height: 35px;
    padding: 8px 12px;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: .5s;
    border-radius: 0 5px 5px 0;
    color: #577BC1;
    font-size: 18px;
    
    
    
}

.header__container .search button:hover{
    background-color: rgb(218, 214, 214);
    

}
.right_container{
    display: flex;
    align-items: center;
    justify-content: space-between;

}
.right_container div{
    cursor: pointer;
}

@media (max-width:767px) {
    .right_container > div:first-child{
        display: none;
    }
    
    
}
@media (min-width:768px) {
    .right_container > div:first-child{
        margin-right: 20px;
    }
    
    
}
.auth{
    display: flex;
    align-items: center;
}
.auth a {
    text-decoration: none;
    color: white;
}
.logout{
    display: flex;
    align-items: center;
    cursor: pointer;
}

.logout i{
    color: white;
    font-size: 22px;
    font-weight: 700;
    cursor: pointer;

}
.auth > div {
    color: white !important;
    font-weight: 500;
    /* background-color: white; */
    /* padding: 7px 12px; */
    border-radius: 5px;
    /* color: #344CB7; */
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;



}
.auth > div h3{
    font-weight: 600;
    
}
.auth div:first-child{
    margin-right: 30px;
}
@media (max-width:567px) {

    .auth div:first-child{
    margin-right: 20px;
}
}
.auth > i {
    color: white;
    /* font-size: 30px; */
    display: none;
    cursor: pointer;
}
.link.on{
    background-color: white;
    color: blue;
}
@media (max-width:500px) {
    .auth div {
        display: none;
    }
    .auth i {
        display: flex;
        font-size: 25px;
    }
    .header__container .logo >i {
