.auth-mobile{
    position: absolute;
    top: 460%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    background-color: #577BC1;
    z-index: 1;
    display: flex;
    width: 90%;
    height: 80vh;
    padding: 20px;
    align-items: center;
    justify-content: space-around;
    border-radius: 10px;
    transition: .5s;
    
    
    
}
.auth-mobile div {
    background-color: white;
    padding: 10px 15px;
    margin: 0 10px;
    color: #577BC1;
    font-weight: 600;
    font-size: 20px;
    border-radius: 10px;
    cursor: pointer;
    transition: .3s;
}
.auth-mobile a {
    color: #577BC1;
    
}
.auth-mobile div:hover{
    background-color: #d9dadd;
}


