.login {
    display: flex;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
}

.signIn_container,
.signUp_container {
    width: 40%;
    border-radius: 10px;
    margin: 20px auto;
    background-color: #577BC1;
    padding: 20px;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    z-index: 10;
}
.signIn_container.of,
.signUp_container.of{
    display: none;
}

.signIn_container > form h3,
.signUp_container > form h3 {
    text-align: center;
    margin-bottom: 20px;
    color: white;
    font-size: 25px;
}
.signIn_container > form ,
.signUp_container > form{

    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;

}
.signIn_container > form div h4,
.signUp_container > form div h4{
    color: white;
    font-size: 18px;
}
.username > input,
.email > input,
.pass > input {
    min-width: 300px;
    max-width: 100%;
    padding: 7px;
    font-size: 18px;
    border-radius: 7px;
    margin: 10px 0;
    border: none;
    
}
.signIn_container form i ,
.signUp_container form i{
    position: absolute;
    top: 0px;
    right: 10px;
    font-size: 23px;
    color: white;
    cursor: pointer;
}
.signIn_container > form button,
.signUp_container > form button{
    padding: 10px 20px;
    cursor: pointer;
    text-align: center;
    width: 35%;
    margin-top: 20px;
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: 7px;
    font-size: 17px;
    background-color: white;
    color: #577BC1;
    font-weight: 700;
    transition: .1s;
    

}
.signIn_container > form button:hover,
.signUp_container > form button:hover{
    background-color: #c0d3f8;
    width: 34%;
}
@media (max-width:992px) {
    .signIn_container,
    .signUp_container{
        width: 60%;
    }
    
}
@media (max-width:767px) {
    .signIn_container,
    .signUp_container{
        width: 70%;
    }
    
}
@media (max-width:567px) {
    .signIn_container, 
    .signUp_container{
        width: 90%;
        
    
    }
    
    .signIn_container > form button, 
    .signUp_container > form button {
        width: 50%;
    }
    
}
@media (max-width:322px) {
    .signIn_container,
    .signUp_container{
        width: 98%;

    }
    .signIn_container > form button,
    .signUp_container > form button{
        width: 50%;
    }
    
}
.sidebar{
    width: 230px;
    height: 100vh;
    max-height: 100vh;
    background-color: #406882;
    position: absolute;
    top: 0;
    right: -100%;
    transition: .3s;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    z-index: 100;
    

    
}
.sidebar.on{
    right: 0;
    z-index: 100;
    
}
.sidebar > .sidebar_header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 5px;
    /* padding-right: 10px; */
    padding: 10px;
    
}


.sidebar_header .profil{
    display: flex;
    align-items: center;
    justify-content: center;
}
.profil .avatar{
    margin-right: 10px;
}
.profil > h4{
    color: white;
    font-size: 18px;
    text-transform: capitalize;
}
.avatar {
    cursor: pointer;
}
.sidebar_header .close_icon {
    font-size: 23px;
    cursor: pointer;
    padding: 4px 10px;
    border-radius: 5px;
    transition: .3s;
    color: white;   
    display: flex;
    align-items: center;

}

.close_icon:hover{
    background-color: white;
    color: #406882;
}

.features{
    flex: 1 1;
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    color: white;
    
    
}
.features > div{
    cursor: pointer;
    /* background-color: #6086a0; */
    
    transition: .3s;
    display: flex;
    align-items: center;
    
}
.features > div:last-child {
    padding: 15px 10px;
}
.features > div a {
    color: white;
    display: flex;
    align-items: center !important;
    /* background-color: black; */
    height: 100%;
    width: 100%;
    padding: 15px 10px;

}

.features > div:hover{
    background-color: #6086a0;
}
.features > div i{
    font-size: 22px;
    display: flex;
    align-items: center;
    margin-right: 15px;
}




